//----- Header
.b-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background-color: transparentize($color-white, .6);
  text-align: right;
  @include vendor-prefixes(transition, .5s);

  &.active {
    background-color: $color-white;
    -webkit-box-shadow: 0px 1px 1px 0px rgba(0,0,0,0.27);
    -moz-box-shadow: 0px 1px 1px 0px rgba(0,0,0,0.27);
    box-shadow: 0px 1px 1px 0px rgba(0,0,0,0.27);
    .hp-btn {
      color: #3BA883;
    }
    .b-header__hamb__span {
      background-color: #3BA883;
    }
    .b-header__logo__sec {
      opacity: 1;
    }
    .c-nav-header__link {
      @include breakpoint(overPhone) {
        color: #3BA883;
      }
    }
    .hp-btn {
      @include breakpoint(overPhone) {
        background-color: $color-white;
        border-color: #3BA883;
      }
    }
    .c-nav-header__item--log {
      &:before {
        background-color: #3BA883;
      }
    }
  }

  &__body {
    padding: 2.2rem 1.7rem;
    @include breakpoint(overPhone) {
      padding: 1.3rem 5rem;
      @include center-block;
      max-width: $site-desk-design;
    }
    @include breakpoint(tablet) {
      padding: 1.3rem 2rem;
    }
  }

  &__logo {
    display: inline-block;
    position: relative;
    vertical-align: middle;
    float: left;
    @include breakpoint(phone) {
      width: 12.7rem;
      height: 2.4rem;
      top: -.4rem;
    }
    @include breakpoint(overPhone) {
      width: 15.5%;
      height: 3.3rem;
      top: .4rem;
    }
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      @include vendor-prefixes(transition, .5s);
    }

    &__sec {
      opacity: 0;
    }
  }

  .hp-btn {
    margin-bottom: 0;
    display: inline-block;
    vertical-align: middle;
    @include vendor-prefixes(transition, .5s);
    @include breakpoint(phone) {
      padding: 0;
      background-color: transparent;
      color: $color-white;
      border: none;
    }
    @include breakpoint(overPhone) {
      padding: 1.2rem;
    }
  }

  &__hamb {
    display: inline-block;
    vertical-align: middle;
    @include hide(overPhone);
    margin-left: 2rem;
  }
  &__hamb__span {
    display: block;
    width: 2.4rem;
    height: .2rem;
    background-color: $color-white;
    margin-bottom: .5rem;
    @include vendor-prefixes(transition, .5s);
    &:last-of-type {
      margin-bottom: 0;
    }
  }
}
